import { acceptHMRUpdate, defineStore } from 'pinia'
import { ref } from 'vue'

export const useTourStore = defineStore('tour', () => {
  /**
   * Current name of the user.
   */
  const step = ref(-1)
  const stepTemplateEditor = ref(-1)

  function setStep(value: number) {
    step.value = value
  }
  function setTemplateEditorStep(value: number) {
    stepTemplateEditor.value = value
  }

  return {
    step,
    setStep,
    stepTemplateEditor,
    setTemplateEditorStep,
  }
})

if (import.meta.hot)
  import.meta.hot.accept(acceptHMRUpdate(useTourStore as any, import.meta.hot))
