<script>
import { mapState, mapWritableState } from 'pinia'

import IconTutorialPointer from '@/assets/icon-tutorial-pointer'
import { useTourStore } from '@/store/TourStore'

export default {
  components: {
    IconTutorialPointer,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    direction: {
      type: String,
      default: 'top',
    },
  },
  data() {
    return {}
  },
  computed: {
    ...mapWritableState(useTourStore, ['step']),
    nextTextButton() {
      console.log('step', this.step)
      if (this.step === 4) {
        return this.$t(`tour.finish`)
      }
      return this.$t(`tour.next`)
    },
  },

  methods: {
    skip() {
      console.log('skip')
      this.step = -1
      //   this.$emit('skip');
    },
    next() {
      console.log('next')
      if (this.step === 3) {
        this.step = -1
      } else {
        const nextStep = this.step + 1
        setTimeout(() => {
          this.step = nextStep
        }, 110)
        this.step = -1
      }
      //   this.$emit('next');
    },
  },

}
</script>

<template>
  <v-tooltip
    :value="show"
    attach
    :top="direction === 'top'"
    :right="direction === 'right'"
    :bottom="direction === 'bottom'"
    :left="direction === 'left'"
    :direction="direction"
    content-class="product-tour"
  >
    <template #activator="{ attrs }">
      <div v-bind="attrs">
        <slot />
      </div>
    </template>

    <v-container v-if="step !== -1" class="stepper pa-4 pb-0">
      <v-row no-gutters class="justify-space-between">
        <div class="pointer" :direction="direction">
          <IconTutorialPointer />
        </div>
        <v-col cols="9">
          <p class="step-title">
            {{ $t(`tour.title[${step}]`) }}
          </p>
        </v-col>
        <v-col cols="3">
          <div class="current-step">
            <p>{{ step + 1 }}/4</p>
          </div>
        </v-col>
      </v-row>
      <v-row no-gutters class="justify-end">
        <v-col cols="12">
          <p class="step-text">
            {{ $t(`tour.text[${step}]`) }}
          </p>
        </v-col>
        <v-col cols="8" class="text-right">
          <v-btn v-if="step < 4" text class="tour-button" @click="skip">
            <p class="skip">
              {{ $t(`tour.skip`) }}
            </p>
          </v-btn>
          <v-btn text class="tour-button" @click="next">
            <p class="next">
              {{ nextTextButton }}
            </p>
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-tooltip>
</template>

