// import People from "@/views/People";
// import Welcome from "./@/views/Welcome";
import type {
  RouteRecordRaw,
} from 'vue-router'

import translationsTemplateEditor from '@/components/TemplateEditor/locales'
// import Authenticate from '@/views/Authenticate.vue'
import Home from '@/views/Home.vue'
// import Invite from '@/views/Invite.vue'
// import SignIn from '@/views/SignIn.vue'
// import SignUp from '@/views/SignUp.vue'

export const SIGNIN = '/SignIn'
export const SIGNUP = '/SignUp'
export const INVITE = '/Invite'

const routesList = ['IssuerProfile', 'Settings']

const children = routesList.map((route) => {
  switch (route) {
    default:
      return {
        path: route,
        name: route,
        component: () =>
          import(
            /* webpackChunkName: "[request]" */ `@/views/${route}.vue`
          ),
        title: route,
        meta: {
          requiresAuth: true,
        },
      }
  }
})

export const routes: RouteRecordRaw[] = [
  {
    path: '/',
    component: Home,
    children: [
      {
        path: 'Dashboard',
        name: 'Dashboard',
        component: async () => await import('@/views/Dashboard.vue'),
        title: 'Dashboard',
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: 'CredentialDesign',
        component: async () => await import('@/views/CredentialDesign/index.vue'),
        title: 'CredentialDesign',
        children: [
          {
            path: '',
            name: 'CredentialDesignView',
            component: async () => await import('@/views/CredentialDesign/index.vue'),
            title: 'View',
            meta: {
              requiresAuth: true,
            },
          },
          {
            path: 'CreateTemplateView',
            name: 'CreateTemplateView',
            component: async () => await import('@/views/CredentialDesign/CreateTemplateView.vue'),
            title: 'CreateTemplateView',
            meta: {
              i18n: translationsTemplateEditor,
              requiresAuth: true,
            },
          },
          {
            path: 'ChoosePresetTemplate',
            name: 'ChoosePresetTemplate',
            component: async () => await import('@/views/CredentialDesign/ChoosePresetTemplate.vue'),
            title: 'ChoosePresetTemplate',
            meta: {
              requiresAuth: true,
            },
          },
        ],
      },
      {
        path: 'Dashboard/ViewTemplate/:tid',
        component: async () => await import('@/views/ViewTemplate.vue'),
        title: 'ViewTemplate',
        meta: {
          requiresAuth: true,
        },
        children: [
          {
            path: '',
            name: 'ViewTemplate',
            component: async () => await import('@/views/ViewTemplate/View.vue'),
            props: r => ({ tid: r.params.tid }),
            title: 'View',
            meta: {
              requiresAuth: true,
            },
          },
          {
            path: 'IssueInBulk',
            name: 'IssueInBulk',
            component: async () => await import('@/views/ViewTemplate/IssueInBulk.vue'),
            title: 'IssueInBulk',
            meta: {
              requiresAuth: true,
            },
          },
          {
            path: 'IssueCredentials',
            name: 'IssueCredentials',
            component: async () => await import('@/views/ViewTemplate/IssueCredentials.vue'),
            title: 'IssueCredentials',
            meta: {
              requiresAuth: true,
            },
          },
        ],
      },
    ].concat(children), // Join the rest of routes (children) from routesList
  },
  {
    path: SIGNIN,
    component: async () => await import('@/views/SignIn.vue'),
    name: SIGNIN,
  },
  {
    path: SIGNUP,
    component: async () => await import('@/views/SignUp.vue'),
    name: SIGNUP,
  },
  {
    path: INVITE,
    component: async () => await import('@/views/Invite.vue'),
    name: INVITE,
  },
  {
    path: '/authenticate/:token',
    component: async () => await import('@/views/Authenticate.vue'),
    name: 'Authenticate',
  },
  {
    path: '/verify/:assetId',
    name: 'Verify',
    component: async () => await import('@/views/Verify/index.vue'),

  },
  {
    path: '/*',
    redirect: '/Dashboard',
  },
]
