<script setup lang="ts">
import { computed, onMounted, ref, watch } from 'vue'
import { useI18n } from 'vue-i18n'

import debug from 'debug'

import LoadingScreen from './components/LoadingScreen.vue'
import { initLang } from './plugins/i18n'
import { useGlobalStore } from './store'

const log = debug('app:App.vue')

const globalStore = useGlobalStore()
const { t: $t } = useI18n()

log('App.vue')

const showLoadingScreen = computed({
  get: () => globalStore.showLoadingScreen,
  set: (value) => {
    globalStore.setShowLoadingScreen(value)
  },
})

const errorMsg = computed({
  get: () => globalStore.errorMsg,
  set: (value) => {
    globalStore.setErrorMessage(value)
  },
})

const snackbarVisibility = ref(false)

watch(errorMsg, (value) => {
  if (value) {
    snackbarVisibility.value = true
  }
})

onMounted(() => {
  initLang()
})
</script>

<template>
  <v-app class="dca-verify-web">
    <v-snackbar v-model="snackbarVisibility" :timeout="2600" color="red">
      {{ $t('errorMsg') }}
      {{ errorMsg }}

      <template #actions="{ isActive }">
        <v-btn color="white" flat v-bind="isActive" @click="errorMsg = ''">
          Close
        </v-btn>
      </template>
    </v-snackbar>
    <LoadingScreen :loading="showLoadingScreen" />
    <v-container fluid class="pa-0">
      <router-view />
    </v-container>
  </v-app>
</template>
