<script setup lang="ts">
import { useAccountStore, useGlobalStore } from '@/store'
import { mapState } from 'pinia'
import { computed, onMounted, ref, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'

import AppFooter from '../layout/AppFooter.vue'
import AppHeader from '../layout/AppHeader.vue'
import { DASHBOARD, PROFILE } from '../store/actions'

const route = useRoute()
const router = useRouter()
const accountStore = useAccountStore()
const globalStore = useGlobalStore()

const overflow = ref(['IssueCredentials'].includes(route.name) ? 'hidden' : '')
const showLoadingScreen = computed({
  get: () => globalStore.showLoadingScreen,
  set: value => globalStore.setShowLoadingScreen(value),
})
const cid = computed(() => accountStore.cid)

watch(route, async () => {
  overflow.value = ['IssueCredentials'].includes(route.name) ? 'hidden' : ''
  console.debug('Update Local data on route change', route, overflow.value, ['IssueCredentials'].includes(route.name))
  try {
    await accountStore[PROFILE]()
    await accountStore[DASHBOARD]()
    showLoadingScreen.value = true
  } catch (error: any) {
    router.push({
      path: '/SignIn',
      query: { redirect: route.path },
    })
    console.error(error.message)
  } finally {
    showLoadingScreen.value = false
  }
})

watch(cid, (value) => {
  console.debug('cid watch value: ', value)
  if (value) {
    accountStore[PROFILE]()
  }
})

onMounted(async () => {
  console.debug('Call PROFILE action')
  try {
    if (cid.value === '') {
      await accountStore[PROFILE]()
    }
    await accountStore[DASHBOARD]()
  } catch (error) {
    console.log('Sign in error', error)
    router.push('/SignIn')
  } finally {
    showLoadingScreen.value = false
  }
})
</script>

<template>
  <v-app>
    <AppHeader />
    <main>
      <v-container class="router-views" :style="{ overflow }">
        <transition origin="center" mode="out-in" :duration="250">
          <router-view />
        </transition>
      </v-container>
    </main>
    <AppFooter />
  </v-app>
</template>

