const logDebug = debug('TemplateStore')

interface TemplateStoreState {
  isPrinting: boolean
  currentLayout: string | null
  components: TemplateElement[]
  layoutBackgroundColor?: string
  activeComponent?: TemplateElement
  selectedTab: string
  uploadedImages: string[]
  uploadedSignatures: string[]
  backSide: boolean
  activeTemplateSide: string
  attributes: any[]
  template: Template
  initialWidth: number
  xDistanceMouseToComponentCenter: number
  yDistanceMouseToComponentCenter: number
  renderedDimensions: number
  repeatedAttributes: boolean
  excelTemplate?: any
}

