export const TemplateEditorTabs = {
  TEMPLATES: 'Templates',
  IMAGES: 'Images',
  SIGNATURES: 'Signatures',
  ATTRIBUTES: 'Attributes',
}

function extractAttributes(components, layout) {
  const arr: any[] = []

  components.forEach((item) => {
    if (item.type === 'text' || (item.dynamicImage && item.text === '[IMAGE]')) {
      const attrs = item.text.match(/\[[A-Z0-9_]*\]/g)

      if (attrs && attrs.length > 0) {
        for (let index = 0; index < attrs.length; index++) {
          const el = attrs[index]
          const attrWithoutBrackets = el.replace(/\[|\]/g, '')
          const newItem = {
            attr: attrWithoutBrackets,
            type: item.type,
            // value: item.value,
            isPublic:
              attrWithoutBrackets.toLowerCase() === 'nome'
              || attrWithoutBrackets.toLowerCase() === 'name'
              || attrWithoutBrackets.toLowerCase() === 'nombre'
              || (attrWithoutBrackets.toLowerCase() === 'username'
              && layout === 'NFT'), // this.publicAttribute,
            isMandatory: item.isMandatory, // adicionar var para marcar se é obrigatorio
            order: item.templateSide === 'front' ? 0 : 1,
          }
          arr.push(newItem)
        }
      }
    }
  })
  return arr
}

function generateLayoutComponentsId(layout) {
  const components: TemplateElement[] = []
  for (let i = 0; i < layout.components.length; i++) {
    const id = Math.random().toString(36).substring(7)
    components.push({
      ...layout.components[i],
      id,
    })
  }
  return { ...layout, components }
}

export const useTemplateStore = defineStore('template', {
  state: (): TemplateStoreState => ({
    isPrinting: false,
    currentLayout: null,
    components: [],
    layoutBackgroundColor: 'unset',
    activeComponent: undefined,
    selectedTab: 'Templates',
    uploadedImages: [],
    uploadedSignatures: [],
    backSide: false,
    activeTemplateSide: 'front',
    attributes: [] /* dynamic text placeholders is a better name */,
    template: {
      name: '',
      idFront: 1,
      backgroundFront: '',
      backgroundBack: '',
    },
    initialWidth: 0,
    xDistanceMouseToComponentCenter: 0,
    yDistanceMouseToComponentCenter: 0,
    renderedDimensions: 0,
    repeatedAttributes: false,
  }),
  actions: {
    addUploadedSignature(value) {
      this.uploadedSignatures.push(value)
    },
    addUploadedImage(value) {
      this.uploadedImages.push(value)
    },
    setTemplateBackground({ templateSide, background }) {
      if (templateSide === 'front') {
        this.template.backgroundFront = background
      } else {
        this.template.backgroundBack = background
        this.backSide = true
      }
    },
    setTemplateName(value) {
      this.template.name = value
    },
    setTemplate(preset, { side, generateComponentIds } = { generateComponentIds: false, side: 'front' }) {
      logDebug('setTemplate', preset)
      const presetTemplate = generateComponentIds ? generateLayoutComponentsId(preset) : { ...preset }
      this.template = presetTemplate
      this.components = presetTemplate.components
      this.currentLayout = preset.currentLayout
      this.repeatedAttributes = false
      this.activeComponent = DEFAULT_ACTIVE_COMPONENT

      if (side) {
        this.activeTemplateSide = side
      }
    },
    setActiveComponent(component) {
      if (!component) {
        this.activeComponent = DEFAULT_ACTIVE_COMPONENT
        // this.activeComponent = undefined
        this.updateComponent({ ...this.activeComponent, isEditable: false, isDraggable: false })
        return
      }
      let tab = ''
      switch (component?.type) {
        case 'image':
          tab = 'Images'
          break
        case 'signature':
          tab = 'Signatures'
          break
        default:
          tab = 'Attributes'
          break
      }

      // get current active component
      if (this.activeComponent?.id !== component.id) {
        this.updateComponent({ ...this.activeComponent, isEditable: false, isDraggable: false })
      }

      this.selectedTab = tab
      this.activeComponent = { ...component, isDraggable: true }
    },
    updateComponent(component) {
      const index = this.components.findIndex(c => c.id === component.id)
      this.components[index] = component
    },
    deleteComponent(component) {
      this.components = this.components.filter(c => c.id !== component.id)
      this.setActiveComponent(undefined)
    },
    setXYDistanceMouseToComponentCenter({ x, y }) {
      this.xDistanceMouseToComponentCenter = x
      this.yDistanceMouseToComponentCenter = y
    },

    async [TEMPLATE_EDITOR_CREATE]({ preview, customTemplateName = 'templateEditor' }) {
      logDebug('CREATE_TEMPLATE', CREATE_TEMPLATE, this.template)

      try {
        // if (
        //   !state.template.backgroundFront.includes(
        //     'files/uploaded/backgrounds',
        //   )
        // ) {
        const file = getFileFromUrl(
          this.template.backgroundFront,
          'backgroundFront',
        )
        const url = await useFileStore()[UPLOAD_FILE]({
          file,
          folder: 'backgrounds',
        }).catch((err) => {
          console.error(err)
          throw err
        })
        logDebug(url)
        this.template.backgroundFront = url
        logDebug(this.template.backgroundFront)

        const data = await useGlobalStore()[CREATE_TEMPLATE]({
          name: this.template.name,
          frontendProps: {
            ...this.template,
            ...{ customTemplateName, preview },
          },
        })
        logDebug(data)
        return this[TEMPLATE_ITEM_EDITOR]({ tid: data.tid })
          .catch((err) => {
            throw err
          })
      } catch (error) {
        console.error(error)
        throw error
      }
    },
    async [TEMPLATE_ITEM_EDITOR]({ tid }) {
      logDebug('TEMPLATE_ITEM')
      const attrs = extractAttributes(
        this.components,
        this.currentLayout,
      )
      const tables = []
      // TO DO: possibilidade de adicionar mais tabelas

      // if (backTemplate && this.backTemplate.length > 0) {
      //   tables.push(this.createTable(this.backTemplate));
      // }

      logDebug('tid', tid)
      logDebug('TEMPLATE_ITEM', TEMPLATE_ITEM)
      logDebug('attrs', attrs)
      const templateItemResult = await useGlobalStore()[TEMPLATE_ITEM]({
        tid,
        attrs,
        tables,
      })

      logDebug(templateItemResult)
      const templateTid = templateItemResult.data.data.template.tid
      return ({ tid: templateTid, name: '' })
    },
  },
  getters: {
    activeComponent(): TemplateElement {
      return this.components.find(item => item?.id === this.activeComponent?.id) || DEFAULT_ACTIVE_COMPONENT
    },
    templateName: state => state.template.name,
    componentIDCount(): number {
      let newId
      // = Math.ceil(Math.random() * 1000000)
      let foundIndex = -1
      const components = Array.from(this.components)

      while (foundIndex > -1 && components.length > 0) {
        newId = Math.ceil(Math.random() * 1000000)
        foundIndex = components.findIndex(item => item.id === newId)
        components.splice(foundIndex, 1)
      }

      return newId
    },
    hasDynamicAttributes(): boolean {
      return this.components.some(item =>
        item.text?.match(/\[[A-Z0-9_]*\]/g),
      )
    },
  },
})
