import { useGlobalStore } from '@/store'
import { acceptHMRUpdate, defineStore } from 'pinia'

import debug from 'debug'

import { useAxiosAuth } from '../api/axios'
import { CredentialState } from '../constants/CredentialStates'

import {
  ACCEPT,
  CREATE_CA,
  CREATE_TEMPLATE,
  DASHBOARD,
  GET_CA,
  GET_TEMPLATE,
  GET_USER,
  IMPORT_FILE,
  ISSUE_USER_CREDENTIAL,
  PROFILE,
  REVOKE,
  TEMPLATE,
  TEMPLATE_ITEM,
  UPDATE_CA,
  UPLOAD_DOC,
} from './actions'

import { useAuthStore } from '@/store/AuthStore'

const logDebug = debug('account:store')

const ROLES = {
  master: 'MASTER',
  manager: 'MANAGER',
}

export interface AccountState {
  cid: string
  adminEmail: string | null
  isMaster: boolean
  caName: string | null
  contractAddress: string | null
  profilePhotoUrl: string | null
  photoHandle: string | null
  templates: any[]
  allPendingUsers: any[]
  allowedTemplates: number
  allowedCredentials: number
  allApprovedCounter: number
  allAdminsTable: any[]
  mastersTable: any[]
  totalIssuedCredentials: number
}

interface User {
  id: string
  tid: string
  status: string
  cred_name: string
  cid: string
  frontendProps: string
}

export const useAccountStore = defineStore('account', {

  state: (): AccountState => ({
    cid: '',
    adminEmail: null,
    isMaster: false,
    caName: null,
    contractAddress: null, // account
    profilePhotoUrl: null, // account
    photoHandle: null, // account
    templates: [], // account
    allPendingUsers: [], // account
    allowedTemplates: 1, // account
    allowedCredentials: 351, // account
    allApprovedCounter: 0, // account
    allAdminsTable: [], // account
    mastersTable: [], // account
    totalIssuedCredentials: 0, // account
  }),

  actions: {
    async [PROFILE]() {
      logDebug('PROFILE ADMIN')
      const authStore = useAuthStore()
      const body = {
        wa: authStore.walletAddress,
      }
      logDebug('body', body)
      const response = await useAxiosAuth().post(PROFILE, {
        ...body,
      })
      const data = response.data.data
      logDebug('PROFILE', data)

      if (data) {
        const admin = {
          name: data.username,
          role: data.no_dca ? ROLES.master : ROLES.manager,
          email: data.email,
          status: 'activeA',
        }
        this.adminEmail = data.email
        if (
          !this.allAdminsTable.some(e => e.username === admin.name)
        ) {
          this.allAdminsTable.push(admin)
        }

        if (data.no_dca) {
          this.mastersTable.push(admin)
        }

        const balances = data.balances
        logDebug('balances', balances)
        if (balances[0]) {
          this.allowedTemplates = balances[0].balances.allowedTemplates
          this.allowedCredentials = balances[0].balances.allowedCredentials
        }
        return data
      } else {
        return null
      }
    },

    [ACCEPT]() {
      logDebug('ACCEPT ADMIN')
      const authStore = useAuthStore()

      return new Promise((resolve, reject) => {
        const body = {
          invite_id: localStorage.getItem('invite_admin'),
          wa: authStore.walletAddress,
        }
        logDebug('body', body)
        useAxiosAuth().post(ACCEPT, {
          ...body,
        })
          .then((response) => {
            logDebug('ACCEPT ADMIN response', response)
            resolve(response.status)
          })
          .catch((err) => {
            console.error(err)
            reject(err)
          })
      })
    },

    async [REVOKE]({ user }) {
      const authStore = useAuthStore()
      const globalStore = useGlobalStore()
      globalStore.setLoading(true)
      logDebug('REVOKE:user', user)

      try {
        const body = {
          id: user.id,
          waAdmin: authStore.walletAddress,
          tid: user.tid,
        }

        const response = await useAxiosAuth().post(REVOKE, body)
        logDebug('REVOKE:response', response)
        return response
      } finally {
        globalStore.setLoading(false)
      }
    },

    [ISSUE_USER_CREDENTIAL]({ data, tid, email, imageArray }) {
      logDebug('ISSUE_USER_CREDENTIAL')
      const authStore = useAuthStore()

      return new Promise((resolve, reject) => {
        const body = {
          cid: this.cid,
          tid,
          wa_admin: authStore.walletAddress,
          data,
          email,
          imgArray: imageArray,
        }
        useAxiosAuth().post(ISSUE_USER_CREDENTIAL, {
          ...body,
        })
          .then((response) => {
            logDebug('ISSUE_USER_CREDENTIAL', response)
            resolve(response.status)
          })
          .catch((err) => {
            console.error(err)
            reject(err)
          })
      })
    },

    [GET_USER]({ id }) {
      logDebug('GET_USER')
      return new Promise((resolve, reject) => {
        useAxiosAuth().get(`${GET_USER}?user_id=${id}`)
          .then((response) => {
            logDebug('GET_USER', response)
            resolve(response.data)
          })
          .catch((err) => {
            console.error(err)
            reject(err)
          })
      },
      )
    },

    [UPDATE_CA]({ name, profilePhotoUrl }) {
      return new Promise((resolve, reject) => {
        const body = {
          cid: this.cid,
          name,
          img_url: profilePhotoUrl,
        }
        useAxiosAuth().post(UPDATE_CA, {
          ...body,
        })
          .then((response) => {
            logDebug('UPDATE_CA', response)
            const data = response.data
            this.contractAddress = data.message.contract_address
            this.cid = data.message.cid

            resolve(data.message.cid)
          })
          .catch((err) => {
            console.error(err)
            reject(err)
          })
      })
    },

    [CREATE_CA]({ admin_email }) {
      logDebug('CREATE_CA', admin_email)
      const authStore = useAuthStore()

      return new Promise((resolve, reject) => {
        // /CA { wallet } => ca_id
        const body = {
          wa: authStore.walletAddress,
          admin_email,
        }
        useAxiosAuth().post(CREATE_CA, {
          ...body,
        })
          .then((response) => {
            logDebug('CREATE_CA', response)
            const data = response.data
            this.contractAddress = data.message.contract_address
            authStore.email = data.message.admin_email

            resolve(data.message.cid)
          })
          .catch((err) => {
            if (err.response.status === 500) {
              reject(err.response.data)
            }

            reject(err)
          })
      })
    },

    async [DASHBOARD]() {
      logDebug('Action DASHBOARD')
      const authStore = useAuthStore()
      const compare = (a, b) => {
        const A = a.name.toUpperCase()
        const B = b.name.toUpperCase()

        let comparison = 0
        if (A > B) {
          comparison = 1
        } else if (A < B) {
          comparison = -1
        }

        return comparison
      }

      // This may cause bugs in reloading
      // commit('setCID', null)

      try {
        const response = await useAxiosAuth().get(DASHBOARD, {
          params: {
            wa: authStore.walletAddress,
          },
        })
        logDebug('DASHBOARD', response)
        const data = response.data.data
        if (data.cid && data.ca_creator === authStore.walletAddress) {
          this.isMaster = true
        }

        // this should be the default url img
        this.profilePhotoUrl = data.img_url

        const orderTemplates = [...data.templates]
        orderTemplates.sort(compare)
        const allPendingUsers: User[] = []
        let totalIssuedCredentials = 0

        orderTemplates.forEach((t) => {
          totalIssuedCredentials += t.users.length
          t.users.forEach((u) => {
            if (u.status === CredentialState.PENDING_APPROVAL) {
              const user = { ...u } as User
              user.cred_name = t.name
              user.cid = t.cid
              user.frontendProps = t.frontendProps
              allPendingUsers.push(user)
            }
            if (u.status === CredentialState.APPROVED) {
              this.allApprovedCounter += 1
            }
          })
        })
        logDebug(allPendingUsers)

        this.totalIssuedCredentials = totalIssuedCredentials
        this.allPendingUsers = allPendingUsers
        this.contractAddress = data.contract_address
        this.templates = orderTemplates
        this.caName = data.ca_name
        this.cid = data.cid

        return { cid: data.cid, templates: data.templates }
      } catch (error) {
        console.log('Error in DASHBOARD', error)
        throw error
      }
    },

    [IMPORT_FILE]({ tid, import_data }) {
      logDebug('IMPORT_FILE call')
      const authStore = useAuthStore()

      return new Promise((resolve) => {
        const body = {
          cid: this.cid,
          tid,
          wa_admin: authStore.walletAddress,
          import_data,
        }
        useAxiosAuth().post(IMPORT_FILE, {
          ...body,
        })
          .then((response) => {
            logDebug(`SUCCESS!! ${response.status}`)
            logDebug(response)
            resolve(response)
          })
          .catch((error) => {
            console.error(error)
          })
      })
    },

    [UPLOAD_DOC]: ({ formData }) => {
      logDebug('UPLOAD_DOC call')
      return new Promise((resolve, reject) => {
        useAxiosAuth().post(UPLOAD_DOC, formData)
          .then((response) => {
            logDebug(`SUCCESS!! ${response.status}`)
            logDebug(response)
            resolve(response)
          })
          .catch((error) => {
            reject(error)
          })
      })
    },

    [CREATE_TEMPLATE]({ name, frontendProps }) {
      const authStore = useAuthStore()

      return new Promise((resolve, reject) => {
        const body = {
          cid: this.cid,
          wa: authStore.walletAddress,
          name,
          frontend_props: frontendProps,
        }

        useAxiosAuth().post(TEMPLATE, {
          ...body,
        })
          .then((response) => {
            logDebug(response)
            resolve(response.data)
          })
          .catch((err) => {
            logDebug(err)
            reject(err)
          })
      })
    },

    [GET_CA]() {
      const authStore = useAuthStore()

      return new Promise((resolve) => {
        const body = {
          cid: this.cid,
          wa_admin: authStore.walletAddress,
        }
        useAxiosAuth().post(GET_CA, {
          ...body,
        })
          .then((response) => {
            logDebug(response)
            resolve(response)
          })
          .catch((err) => {
            logDebug(err)
          })
      })
    },

    [TEMPLATE_ITEM]({ tid, attrs, tables }) {
      const authStore = useAuthStore()

      return new Promise((resolve, reject) => {
        const body = {
          cid: this.cid,
          tid,
          wa: authStore.walletAddress,
          attrs,
          tables,
        }
        useAxiosAuth().post(TEMPLATE_ITEM, {
          ...body,
        })
          .then((response) => {
            logDebug(response)
            resolve(response)
          })
          .catch((err) => {
            logDebug(err)
            reject(err)
          })
      })
    },

    [GET_TEMPLATE]({ tid }) {
      return new Promise((resolve) => {
        useAxiosAuth().get(`${TEMPLATE}?tid=${tid}`)
          .then(({ data }) => {
            resolve(data)
          })
          .catch((err) => {
            logDebug(err)
          })
      })
    },
  },
})

if (import.meta.hot)
  import.meta.hot.accept(acceptHMRUpdate(useAccountStore as any, import.meta.hot))
