import { defineStore } from 'pinia'
import { type Ref, ref } from 'vue'

/** Global Store */
export default defineStore('global', () => {
  // State

  /** Loading overlay */
  const loading: Ref<boolean> = ref(true)
  const showLoadingScreen: Ref<boolean> = ref(true)
  /** ProgressBar Percentage */
  const progress: Ref<number | null> = ref(null)
  /** SnackBar Text */
  const errorMsg: Ref<string> = ref('')

  // debug: null, // global
  //   loading: false, // global
  //   isLoading: true, // global
  // errorMsg: null, // global
  // Actions

  /**
   * Show loading Overlay
   *
   * @param display - visibility
   */
  function setLoading(display: boolean): void {
    loading.value = display
    if (!display) {
      // Reset Progress value
      progress.value = null
    }
  }
  /**
   * Show LoadingScreen Overlay
   *
   * @param display - visibility
   */
  function setShowLoadingScreen(display: boolean): void {
    showLoadingScreen.value = display
  }

  /**
   * Update progress value
   *
   * @param v - progress value
   */
  function setProgress(v: number | null = null): void {
    // update progress value
    progress.value = v
    // display loading overlay
    loading.value = true
  }

  /**
   * Show snackbar message
   *
   * @param msg - snackbar message
   */
  function setErrorMessage(msg = ''): void {
    // put snackbar text
    errorMsg.value = msg
  }

  return { loading, progress, errorMsg, showLoadingScreen, setLoading, setProgress, setErrorMessage, setShowLoadingScreen }
})
