import { useAuthStore, useGlobalStore } from '@/store'
import {
  type NavigationGuardNext,
  type RouteLocationNormalized,
  type Router,
  createRouter,
  createWebHistory,
} from 'vue-router'

// Routes
import debug from 'debug'

import { routes } from '@/routes'

const logDebug = debug('app:router')
// Components
// import HomeView from '@/views/HomeView.vue'

// Pinia Store

// Unimplemented in Vuetify 3.5.6
// import { goTo } from 'vuetify/services';

/** Vue Router */
const router: Router = createRouter({
  /**
   * History Mode
   *
   * @see {@link https://router.vuejs.org/guide/essentials/history-mode.html }
   */
  history: createWebHistory(import.meta.env.BASE_URL), // createWebHashHistory(import.meta.env.BASE_URL)
  /*
  scrollBehavior: (to, _from, savedPosition) => {
    let scrollTo: number | string = 0;

    if (to.hash) {
      scrollTo = to.hash;
    } else if (savedPosition) {
      scrollTo = savedPosition.top;
    }
    return goTo(scrollTo);
  },
  */
  routes,
})

// Global before guards
// https://router.vuejs.org/guide/advanced/navigation-guards.html#global-before-guards}
router.beforeEach(
  async (
    to: RouteLocationNormalized,
    from: RouteLocationNormalized,
    next: NavigationGuardNext,
  ) => {
    const globalStore = useGlobalStore()
    // Show Loading
    // comment out for https://github.com/logue/vite-vuetify-ts-starter/issues/16
    // globalStore.setLoading(true);

    // Hide snack bar
    globalStore.setErrorMessage('')

    logDebug('toPath', to)

    // if (to.meta.i18n) {
    //   Object.keys(to.meta.i18n).forEach(lang =>
    //     i18n.mergeLocaleMessage(lang, to.meta.i18n[lang]),
    //   )
    // }

    const invite = to.query.invite_admin

    if (invite) {
      localStorage.setItem('invite_admin', invite.toString())
    }

    const accessToken = useAuthStore().accessToken
    logDebug('accessToken', accessToken)

    if (to.matched.some(record => record.meta.requiresAuth)) {
      if (accessToken) {
        console.log('Continue')
        next()
        return
      }
      next('/SignIn')
    } else {
      if (to.path === '/') {
        debug('Home Path')
        return next('/Dashboard')
      } else if (to.path === '/SignIn' && accessToken) {
        console.log('Continue', to.path)
        return next('/Dashboard')
      } else {
        console.log('Continue', to.path)
        return next()
      }
    }

    // next()
  },
)

// Global After Hooks
// https://router.vuejs.org/guide/advanced/navigation-guards.html#global-after-hooks}
router.afterEach(() => {
  const globalStore = useGlobalStore()
  // Hide Loading
  globalStore.setLoading(false)
})

