import { defineStore } from 'pinia'

import { UPLOAD_FILE } from './actions'

const API_URL = import.meta.env.VITE_APP_API_URL

interface FileStoreState {
  LOGOS_FOLDER: string
  photos: { url: string }[]
}

export const useFileStore = defineStore('file', {
  state: (): FileStoreState => ({
    LOGOS_FOLDER: 'wallid-template-logos/',
    photos: [],
  }),
  actions: {
    photoUpload(value) {
      this.photos.push({ url: value })
    },
    async [UPLOAD_FILE]({ file, folder }) {
      console.log('UPLOAD_FILE Action', file, folder)
      const targetFolder = folder ?? this.LOGOS_FOLDER
      try {
        // upload a file to API_URL endpoint
        const formData = new FormData()
        formData.append('file', file)
        const response = await fetch(`${API_URL}/ftp/${targetFolder}`, {
          method: 'POST',
          body: formData,
          headers: {
            Authorization: `Bearer ${sessionStorage.getItem('accessToken')}` || '',
          },
        })
        const data = await response.json()
        console.log('UPLOAD_FILE Action', data)
        this.photoUpload(data.url)
        return data.url
      } catch (err) {
        console.log(err)
      }
    },
  },
})
