import { defineStore } from 'pinia'

import axios, { useAxiosAuth } from '../api/axios'

import { AUTHENTICATE, SIGN_IN, SIGN_OUT } from './actions'

const ACCESS_TOKEN = 'accessToken'
const WALLET_ADDRESS = 'walletAddress'

interface AuthState {
  accessToken: string
  walletAddress: string
  email: string
}

interface LoginData {
  username: string
  password: string
}

export const useAuthStore = defineStore('auth', {
  state: (): AuthState => ({
    accessToken: sessionStorage.getItem(ACCESS_TOKEN) || '',
    walletAddress: sessionStorage.getItem(WALLET_ADDRESS) || '',
    email: '',
  }),
  actions: {
    async [SIGN_IN](loginData: LoginData): Promise<void> {
      try {
        const { data, status } = await axios.post('/auth/login', {
          username: loginData.username,
          password: loginData.password,
        })
        if (status !== 200 || !data.token) {
          throw new Error('Invalid credentials')
        }

        this.setAccessToken(data.token)
        this.setWalletAddress(data.walletAddress)
        this.setUserEmail(data.email)
      }
      catch (err: any) {
        console.error('SIGN IN error: ', err.response)
        sessionStorage.removeItem(ACCESS_TOKEN)
        sessionStorage.removeItem(WALLET_ADDRESS)
        if (err.response.data.errorStatus) {
          throw new Error(err.response.data.errorStatus)
        }
        throw err
      }
    },
    [SIGN_OUT]() {
      console.debug('Action SIGN_OUT')
      return new Promise((resolve) => {
        this.setAccessToken('')
        this.setWalletAddress('')
        this.setUserEmail('')

        sessionStorage.removeItem(ACCESS_TOKEN)
        sessionStorage.removeItem(WALLET_ADDRESS)
        resolve(true)
      })
    },
    async [AUTHENTICATE]({ token }) {
      console.debug('Action AUTHENTICATE', token)
      try {
        this.setAccessToken(token)

        const { data, status } = await useAxiosAuth().get('/auth/profile')
        if (status !== 200) {
          throw new Error('Invalid credentials')
        }
        this.setUserEmail(data.email)
        this.setWalletAddress(data.walletAddress)
      }
      catch (err: any) {
        console.error('AUTHENTICATE error: ', err.response)
        if (err.response.data.errorStatus) {
          throw new Error(err.response.data.errorStatus)
        }
        throw err
      }
    },
    setAccessToken(value: string) {
      this.accessToken = value
      if (value) {
        sessionStorage.setItem(ACCESS_TOKEN, value)
      }
    },
    setWalletAddress(value: string) {
      this.walletAddress = value
      if (value) {
        sessionStorage.setItem(WALLET_ADDRESS, value)
      }
    },
    setUserEmail(value: string) {
      this.email = value
    },
  },
})
