import type { Locale } from 'vue-i18n'
import { createI18n } from 'vue-i18n'

import en from '@/locales/languages/en'

export const I18N_LOCALE_KEY = 'locale'

// Import i18n resources
// https://vitejs.dev/guide/features.html#glob-import
//
// Don't need this? Try vitesse-lite: https://github.com/antfu/vitesse-lite
const i18n = createI18n({
  legacy: false,
  locale: 'en',
  messages: {
    en,
  },
  fallbackLocale: 'en',

})

const localesMap = Object.fromEntries(
  Object.entries(import.meta.glob('../locales/languages/*.js'))
    .map(([path, loadLocale]) => [path.match(/([\w-]*)\.js$/)?.[1], loadLocale]),
) as Record<Locale, () => Promise<{ default: Record<string, string> }>>

export const availableLocales = [
  { id: 'pt', name: 'Português' },
  { id: 'en', name: 'English' },
  { id: 'es', name: 'Español' },
]

const loadedLanguages: string[] = []

function setI18nLanguage(lang: Locale) {
  i18n.global.locale.value = lang as any
  console.log('setI18nLanguage', lang, i18n.global.locale.value)

  localStorage.setItem(I18N_LOCALE_KEY, lang)

  if (typeof document !== 'undefined')
    document.querySelector('html')?.setAttribute('lang', lang)
  return lang
}

export async function loadLanguageAsync(lang: string): Promise<Locale> {
  // If the same language
  console.log('same language', i18n.global.locale.value === lang, ',', i18n.global.locale.value, ',', lang)

  if (i18n.global.locale.value === lang)
    return setI18nLanguage(lang)
  console.log('loadedLanguages', loadedLanguages)

  // If the language was already loaded
  if (loadedLanguages.includes(lang))
    return setI18nLanguage(lang)

  // If the language hasn't been loaded yet
  const messages = await localesMap[lang]()
  console.log('messages', messages)

  i18n.global.setLocaleMessage(lang, messages.default as any)
  loadedLanguages.push(lang)
  return setI18nLanguage(lang)
}

export function initLang() {
  const browserLang = navigator.language.substring(0, 2)
  const lang = localStorage.getItem(I18N_LOCALE_KEY) || browserLang

  const availableLocale = availableLocales.find(locale => locale.id === lang)
  console.log('availableLocale', availableLocale)

  if (availableLocale) {
    loadLanguageAsync(availableLocale.id)
  }
  else {
    loadLanguageAsync('en')
  }
}

export default i18n
